<template>
	<div class="player">
		<div ref="play" class="play"></div>
	</div>
</template>

<script>
	import 'DPlayer/dist/DPlayer.min.css';
	import DPlayer from 'DPlayer';
	import video from "../../static/js/video.json";
	export default {
		props: ["animateID","videoIndex"],
		data(){
			return {
				videoList: {},
				videoUrl: '',
				options: {
				    container: '',
				    autoplay: false,
				    theme: '#FADFA3',
				    loop: true,
				    lang: 'zh-cn',
				    screenshot: true,
				    hotkey: true,
				    preload: 'auto',
//				    logo: 'logo.png',
				    volume: 0.7,
				    mutex: true,
				    video: {
				    	url: 'blob:https://www.iqiyi.com/57ea2116-4e8d-4b56-823c-076f79e76d11',
//				        url: 'http://1251883823.vod2.myqcloud.com/6b94ca32vodsh1251883823/d744a6895285890782304152843/mjQcm1ClBSQA.mp4',
//				        pic: 'dplayer.png',
//				        thumbnails: 'thumbnails.jpg',
				        type: 'auto'
				    }
				}
			}
		},
		watch: {
			videoIndex(newVal){
				this.dp.pause();
				this.dp.switchVideo({
				    url: this.videoUrl,
//				    pic: 'second.png',
//				    thumbnails: 'second.jpg'
				});
				this.dp.play();
				this.selectVideo();
//				console.log(this.videoUrl)
//				console.log(newVal)
			}
		},
		mounted(){
			this.newPlay();
			this.videoList = video.videoJSON;
			this.selectVideo();
		},
		methods:{
			selectVideo(){
				for(let i = 0; i< this.videoList.length; i++){
					if(this.videoList[i].animate == this.animateID){
						for(let video in this.videoList[i].list){
							if(video == this.videoIndex){
								this.videoUrl = this.videoList[i].list[video]
							}
						}
					}
				};
			},
			newPlay(){
				this.options.container = this.$refs.play;
				this.dp = new DPlayer(this.options);
			}
		}
	}
</script>

<style lang="less">
	.player{
		.play{
			height: 4.6rem;
		}
	}
</style>


